import Topbar from '../../components/topbar.vue'
export default {
	data() {
		return {
			isPhoneLogin:true,
			topbarData: {
				leftHandle: false,
				title: "验证原手机号",
				left: "《",
				right: ""
			},
			phone:'',// 手机号码
			yzcode:'',//验证码
			username:'',// 用户名
			password:"", // 密码
			
		}
	},
	methods: {
		// 登录注册
		loginOrResign(){
			let _this = this 
			let obj = {}
			let url = ''
			if (this.isPhoneLogin) {
				url = '/api/user/mobilelogin'
				if (!this.phone) {
					uni.showModal({
						title: '提示',
						content: '请输入手机号码',
						showCancel: false,
					});
					return
				}
				
				if (!this.yzcode) {
					uni.showModal({
						title: '提示',
						content: '请输入验证码',
						showCancel: false,
					});
					return
				}
				
				obj.mobile = this.phone,
				obj.captcha = this.yzcode
			} else {
				url = '/api/user/login'
				if (!this.username) {
					uni.showModal({
						title: '提示',
						content: '请输入用户名',
						showCancel: false,
					});
					return
				}
				
				if (!this.password) {
					uni.showModal({
						title: '提示',
						content: '请输入密码',
						showCancel: false,
					});
					return
				}
				obj.account = this.username,
				obj.password = this.password
			}
			console.log(obj)
			uni.request({
				url:_this.$url+url,
				method:'POST',
				
				data:obj,
				success:function(res){
					if (res.data.code == 1) {
						uni.showToast({
							title: res.data.msg,
							// icon:"none"
						});
						_this.$store.commit('setUserInfo',res.data.data.userinfo)
						setTimeout(()=>{
							uni.switchTab({
								url:'../index/index'
							})
						},1000)
					} else {
						uni.showToast({
							title: res.data.msg,
							icon:"none"
						});
					}
				}
			})
		},
		
		
		// 改变登录方式
		changeLoginWay(type){
			this.isPhoneLogin = !this.isPhoneLogin
		},
		
		
		// 获取验证码
		getYzcode(){
			let phone = this.phone
			if (phone.length != 11) {
				uni.showModal({
					showCancel:false,
					title:'提示',
					content:'请输入正确手机号码'
				})
				return
			}

			uni.request({
				url:'/sms/send',
				method:'GET',
				data:{
					mobile:phone
				},
				success:function(res){
					console.log(res)
				}
			})
		},
		
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
	},
	components: {
		Topbar
	}
}
